@import 'reset';

/* button */
.container_button_scan_qr_code{
    text-align: center;

}

.button{
    text-transform: uppercase;
    padding: 8.5px 15px;
    margin: 25px 0;
    cursor: pointer;
    background: #43b348;
    color: white;
    border: none;
    outline: none;
    -webkit-box-shadow: 0px 0px 4px 0px #8e8b8b;
            box-shadow: 0px 0px 4px 0px #8e8b8b;
    -webkit-border-radius: 4px;
            border-radius: 4px;
    font-size: 1em;

    &:focus{
        background-color: #37923b;
    }
}

.stop{
    margin: 15px;
}

.content{
    color: white;
    font-size: 17px;
}

/* container video */

.container_video{
    background-color: rgba(0, 0, 0, 0.40);
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: none;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
    -webkit-align-items: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
        -ms-flex-pack: center;
            justify-content: center;

    &.active{
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
    }

    .wrapper{
        position: relative;
        z-index: 0;
        width: 100%;
        height: 100%;
        max-width: 500px;
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
        -webkit-align-items: center;
            -ms-flex-align: center;
                align-items: center;
        -webkit-box-pack: center;
        -webkit-justify-content: center;
            -ms-flex-pack: center;
                justify-content: center;
    }

    .button_lamp{
        position: absolute;
        top: 0.5em;
        right: 0.5em;
        padding: 6px;
        cursor: pointer;
        outline: none;
        border: none;
        -webkit-border-radius: 4px;
                border-radius: 4px;
        background-color: #f2f0ef;
        display: -webkit-inline-box;
        display: -webkit-inline-flex;
        display: -ms-inline-flexbox;
        display: inline-flex;
        z-index: 1;
    }

    .scaner_line{
        position: absolute;
        z-index: 1;
        width: 80%;
        height: 2px;
        left: 10%;
        top: 49%;
        overflow: hidden;
        background-color: tomato;
        -webkit-animation: scan 1s ease infinite;
                animation: scan 1s ease infinite;
        -webkit-transform: translateZ(0);
                transform: translateZ(0);
    }

    .video{
        width: 100%;
        height: 100%;
        position: relative;
        z-index: 0;
    }
}

@-webkit-keyframes scan {
    0% { opacity: 0; }
    100% { opacity: 1; }
}

@keyframes scan {
    0% { opacity: 0; }
    100% { opacity: 1; }
}


